import Tooltip from './src/components/Tooltip.vue'
import Vue from 'vue'
// 
if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('Tooltip',Tooltip)
}
// Vue.use
Tooltip.install = function() {
  Vue.component(Tooltip.name,Tooltip)
}
export default Tooltip


